@media (prefers-color-scheme: dark) {
  :root {
    --main-bg-color: #282c34;
    --text-no-background-color: #ffffff;
    --over-image-color: #ffffff;
    --accent-color: #6bb9e1;
    --highlighted-accent-color: #83d6ff;
    --link-color: #6be1be;
  }
}

@media (prefers-color-scheme: light) {
  :root {
    --main-bg-color: #ffffff;
    --text-no-background-color: #000000;
    --over-image-color: #ffffff;
    --accent-color: #6be1be;
    --highlighted-accent-color: #84ffda;
    --link-color: #6bb9e1;
  }
}

.App {
  background-color: var(--main-bg-color);
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--text-no-background-color);
  min-height: 100vh;
  background-image: url("tragic_death.jpg");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  text-align: center;

}

.Title {
  color: var(--text-no-background-color);
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
}

a {
  color: var(--link-color)
}

.OverImage {
  color: var(--over-image-color);
}

.App-body {
  color: var(--text-no-background-color);
  justify-content: center;
  width: fit-content;
  margin: auto;
}

p {
  margin: 0;
}

.FactBox {
  width: 20vw;
  height: fit-content;
  min-height: min(30vh, 30vw);
  background-color: var(--accent-color);
  margin: 1vh;
  margin-left: 1vw;
  margin-right: 1vw;
  font-size: min(2vw, 3vh);
  text-align: center;
}

.FactText {
  margin: auto;
}

.FactLine {
  display: flex;
  margin: 0px;
  padding: 0px;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}

.CenteredText {
  text-align: center;
}

.Hashtag {
  color:var(--accent-color);
}

.ShareButton {
  margin-top: 2.5vh;
  margin-bottom: 2.5vh;
  height: 2vh;
  padding-left: 10px;
  padding-right: 10px;
  background-color: var(--accent-color);
  border: none;
  transition-duration: 0.4s;
  border-radius: 6px;
  color: var(--text-no-background-color);
}

.ShareText {
  color: var(--text-no-background-color)
}

.ShareButton:hover {
  border-radius: 3px;
  background-color: var(--highlighted-accent-color);
}

.DONT {
  font-weight: bolder;
  font-size: min(4vw, 6vh);
}